                        <div id="itemCatatanPerwalian">
                            <span v-if="catatan.tipe === 'refleksi'" id="garisMahasiswa"></span>
                            <span v-else-if="catatan.tipe === 'khusus'" id="garisOrangTua"></span>

                            <div class="row">
