                                                </v-tooltip>
                                            </span>
                                        </p>
                                    </div>
                                    </div>
                                    <p>12 Oktober 2022 10:00 WIB - 11:00 WIB</p>
                                </div>
                            </div>

                            <div id="agendaPerwalian">
                                <p>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Ullam, eos quisquam corrupti nesciunt officia beatae rem odio mollitia culpa nulla nemo tenetur modi nam aspernatur totam, iste sed architecto explicabo, sequi ratione praesentium esse provident sint expedita? Laborum nisi ea obcaecati culpa exercitationem doloremque... 
                                    <router-link :to="{name:'EditCatatanRefleksiDosen'}">
                                        <b><u>Lihat selengkapnya</u></b>
                                    </router-link>
                                </p>
                            </div>

                            <div id="tombolEditAchive" class="d-flex">
                                <div class="col-6 px-0 ">
                                    <router-link :to="{name:'EditCatatanRefleksiDosen'}" id="buttonEdit" type="button" class="btn btn-light">
