<script>
import BreadcrumbModule from './BreadcrumbModule.vue';
    class Catatan{
        constructor(judul, tipe){
            this.judul = judul;
            this.tipe = tipe;
        }
    }

    export default {
        name:"LihatSemuaCatatanMahasiswa",
        components:{
            BreadcrumbModule
        },
        data(){
            return{
