                isOn:false,
                isOn2:false,
                page: 1,
                perPage: 6,
                filters:[
                    "Paling baru dibuat",
                    "Paling lama dibuat",
                ],
                listCatatan:[
                    new Catatan("Catatan Perwalian Mahasiswa","mahasiswa"),
                    new Catatan("Catatan Perwalian Orang Tua","orangTua"),
                    new Catatan("Catatan Perwalian Orang Tua & Mahasiswa","orangTuaMahasiswa"),
                    new Catatan("Catatan Perwalian Mahasiswa","mahasiswa"),
                    new Catatan("Catatan Perwalian Orang Tua","orangTua"),
                    new Catatan("Catatan Perwalian Orang Tua & Mahasiswa","orangTuaMahasiswa"),
                    new Catatan("Catatan Perwalian Mahasiswa","mahasiswa"),
                    new Catatan("Catatan Perwalian Orang Tua","orangTua"),
                    new Catatan("Catatan Perwalian Orang Tua & Mahasiswa","orangTuaMahasiswa"),
                    new Catatan("Catatan Perwalian Mahasiswa","mahasiswa"),
                    new Catatan("Catatan Perwalian Orang Tua","orangTua"),
                    new Catatan("Catatan Perwalian Orang Tua & Mahasiswa","orangTuaMahasiswa"),
                    new Catatan("Catatan Perwalian Orang Tua","orangTua"),
                    new Catatan("Catatan Perwalian Orang Tua & Mahasiswa","orangTuaMahasiswa"),
                    new Catatan("Catatan Perwalian Mahasiswa","mahasiswa"),
                    new Catatan("Catatan Perwalian Orang Tua","orangTua"),
                    new Catatan("Catatan Perwalian Orang Tua & Mahasiswa","orangTuaMahasiswa"),
                    
                ]
            }
        },methods: {
            showHistory(){
                this.isHistory = !this.isHistory
            },
            hapusCatatanTidak(){
                this.isOn2 = false;
            },
            hapusCatatanYa(){
                this.deleteCatatan(this.indexHapusCatatan);
                this.isOn2 = false;

                var x = document.getElementById("snackbar");
                x.className = "show";
                setTimeout(function(){ x.className = x.className.replace("show", ""); }, 3000);
            },
            showPopUpPreferensi(){
                this.isOn = true;
            },
            batal(){
                this.isOn = false;
            },
            simpan(){
                this.isOn = false;
            },
            showPopUpPreferensi2(index){
                this.indexHapusCatatan = index;
                this.isOn2 = true;
            },
            editCatatan(){
                window.open(this.$router.resolve("/editCatatanMahasiswa").href, '_blank');
            },
            deleteCatatan(index){
                this.listCatatan.splice(index,1);
            },
            bukaArsip(){
                window.open(this.$router.resolve('/arsipPerwalianDosen').href, '_blank');
            },
            scrollTop(){
                window.scrollTo({
                    top: 0,
                    behavior: "smooth" // Animasi smooth scrolling
                });
            },
        },
        updated(){
            this.scrollTop();
        },
        mounted(){
            //tool tip
            // $(function(){
            //     $('[data-toggle="tooltip"]').tooltip();   
            // });

            const script1 = document.createElement("script");
            script1.src = "https://code.jquery.com/jquery-3.5.1.slim.min.js";
            script1.integrity =
            "sha384-DfXdz2htPH0lsSSs5nCTpuj/zy4C+OGpamoFVy38MVBnE+IbbVYUew+OrCXaRkfj";
            script1.crossOrigin = "anonymous";
            document.head.appendChild(script1);

            //popper.min.js
            const script2 = document.createElement("script");
            script2.src ="https://cdn.jsdelivr.net/npm/popper.js@1.12.9/dist/umd/popper.min.js";
            script2.integrity = "sha384-hSM2mzKd06KfNmOz6mK6+sfuLdYVjI1MKOpnE+O+hNEZmZ+zQp8hJz3uPL2twNJX";
            script2.crossOrigin = "anonymous";
            document.head.appendChild(script2);

            //bundle.min.js
            const script3 = document.createElement("script");
            script3.src ="https://cdn.jsdelivr.net/npm/bootstrap@4.5.3/dist/js/bootstrap.bundle.min.js";
            script3.integrity = "sha384-ho+j7jyWK8fNQe+A12Hb8AhRq26LrZ/JpcUGGOn+Y7RsweNrtN/tE3MoK7ZeZDyx";
            script3.crossOrigin = "anonymous";
            document.head.appendChild(script3);

        }
        ,
        created(){
            this.scrollTop();
        },
        computed: {
            displayedCatatan() {
            // Menghitung indeks item pertama dan terakhir pada halaman saat ini
            const startIndex = (this.page - 1) * this.perPage
            const endIndex = startIndex + this.perPage

            // Mengembalikan subset catatan yang akan ditampilkan pada halaman saat ini
            const hasil = this.listCatatan.slice(startIndex, endIndex)
            return hasil
            }
        }


    }
</script>

